#hamburger {
  text-transform: uppercase;
  font-weight: 300;
  font-family: "Roboto Condensed";
}
#hamburger,
#hamburger > * {
  box-sizing: border-box;
}
#hamburger .toggle {
  position: fixed;
  z-index: 910;
  top: 0;
  right: 0;
  padding: 0;
  opacity: 0;
  height: 60px;
  width: 60px;
  border-bottom-left-radius: 30px;
  font-size: 30px;
  line-height: 60px;
  text-align: center;
  background-color: #00bce3;
}
#hamburger .toggle.enabled {
  opacity: 1;
}
#hamburger .toggle a {
  position: relative;
  top: -0.05em;
  right: -0.05em;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
}
#hamburger .toggle a:before {
  content: '\f0c9';
}
#hamburger .toggle.active a:before {
  content: '\f00d';
}
#hamburger .menu {
  transition: all 250ms ease-in;
  background-color: #00bce3;
  white-space: nowrap;
  overflow: hidden;
  position: fixed;
  z-index: 900;
  height: 100%;
  width: 75%;
  right: 0;
  top: 0;
  margin: 0;
  padding: 0;
  font-size: 1em;
  line-height: 1.4em;
  font-size: 8vmin;
  line-height: 12vh;
  border-bottom-left-radius: 30px;
  transform: translate(100%, -100%) scale(0);
}
@media (min-width: 480px) {
  #hamburger .menu {
    font-size: 6vmin;
  }
}
@media (min-width: 768px) {
  #hamburger .menu {
    font-size: 5vmin;
  }
}
#hamburger .menu.enabled {
  border-bottom-left-radius: 25vmin;
  transform: translate(0, 0) scale(1);
}
#hamburger .menu ul {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  white-space: nowrap;
  overflow: hidden;
}
#hamburger .menu ul li,
#hamburger .menu ul li > a {
  color: #fff;
}
#hamburger .menu ul li:active,
#hamburger .menu ul li > a:active,
#hamburger .menu ul li:hover,
#hamburger .menu ul li > a:hover {
  background-color: transparent;
  text-decoration: none;
}
#hamburger .menu ul li {
  display: block;
  margin: 0;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
}
.anchor-target {
  position: relative;
}
.anchor-target a.anchor-link {
  position: absolute;
  display: block;
  left: 0;
  line-height: 1;
}
.anchor-target a.anchor-link i.fa {
  font-size: 0%;
  font-weight: 400;
  opacity: 0;
  transition: opacity 150ms ease-in, margin 150ms ease-in, font-size 150ms ease-in;
  display: inline-block;
  vertical-align: middle;
  padding: 0.6em 0.6em 0.6em 0;
}
.anchor-target a.anchor-link i.fa:before {
  content: '\f0c1 ';
}
.anchor-target:hover a.anchor-link i.fa,
.anchor-target a.anchor-link:hover i.fa {
  font-size: 50%;
  margin-left: -1.2em;
  opacity: .6;
}
.anchor-target:hover a.anchor-link i.fa:hover,
.anchor-target a.anchor-link:hover i.fa:hover {
  opacity: .8;
}
